const defaultHeader = 'BasicTitle';
const mapReport = () => Object.keys(validReport).map((n) => {
  const { link, components } = validReport[n];
  return {
    path: `${link}/:gametype/:gameid`,
    name: link,
    components: {
      reportType: () => import('@/application/Report/View/ReportTables/' + components.reportType + '.vue'),
      reportFilter: () => import('@/application/Report/View/ReportFilters/' + components.reportFilter || defaultHeader + '.vue'),
    },
  };
});
