import Vue from 'vue'
import VueRouter, { RouteConfig } from 'vue-router'
import { mapReport } from './report';

Vue.use(VueRouter)

  const routes: Array<RouteConfig> = [
  // {
  //   path: '/',
  //   name: 'Home',
  //   alias: 
  // },
  {
    path: '/home',
    name: 'home',
    alias: '/',
    components: {
      navigation: () => import('@/application/Common/View/Nav.vue'),
      default: () => import('@/views/Home.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/two-sides',
    name: 'two-sides',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/TwoSides.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/recommend',
    name: 'recommend',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/Recommend.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/proplan/:rank/:type/:order/:proId/:gameId',
    name: 'proplan',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/PlanHistory.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/reminder',
    name: 'reminder',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/Reminder.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/about/:title',
    name: 'about',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/About.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/killplan/:gameId?',
    name: 'killplan',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/KillPlan.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/about',
    name: 'About',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/About.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/quizes',
    name: 'quizes',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/Quiz.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/login',
    name: 'Login',
    component: () => import('@/views/Login.vue')
  },
  {
    path: '/register',
    name: 'Register',
    component: () => import('@/views/Register.vue')
  },
  {
    path: '/feedback',
    name: 'feedback',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/FeedBack.vue'),
    }
  },
  {
    path: '/instruction',
    name: 'instruction',
    component: () => import('@/views/Instruction.vue')
  },
  {
    path: '/gamerules',
    name: 'gamerules',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/GameRules.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/buyplan',
    name: 'buyplan',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/BuyPlan.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    }
  },
  {
    path: '/report',
    name: 'report',
    components: {
      navigation: () => import('@/application/Common/View/ShortNav.vue'),
      default: () => import('@/views/Report.vue'),
      footer: () => import('@/application/Common/View/Footer.vue'),
    },
    children: mapReport(),
  },
  {
    path: '/roadbeadsexample',
    name: 'roadbeadsexample',
    component: () => import('@/components/RoadBeads.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
