import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'

import MarqueeText from 'vue-marquee-text-component';
import VueAwesomeSwiper from 'vue-awesome-swiper';
import 'xe-utils'
import {
  Table,
  Column,
  Header,
} from 'vxe-table'
import {
  Grid,
  GridItem,
  Divider,
  NavBar,
  Button,
  Popup,
  Tab,
  Tabs,
  Icon,
  Calendar,
  Field,
  Form,
  Picker,
  Progress,
  CountDown,
  Cell,
  CellGroup
} from 'vant';
import { Locale } from 'vant';
import zhCN from 'vant/lib/locale/lang/zh-CN';

Locale.use('zh-CN', zhCN);

import 'swiper/swiper-bundle.min.css';
import 'vxe-table/lib/index.css'
import 'vant/lib/index.css';
import './resources/scss/main.scss';

Vue.use(Table);
Vue.use(Column);
Vue.use(Header);
Vue.component('marquee-text', MarqueeText);
Vue.use(VueAwesomeSwiper);

Vue.use(Grid);
Vue.use(GridItem);
Vue.use(Divider);
Vue.use(NavBar);
Vue.use(Button);
Vue.use(Popup);
Vue.use(Tab);
Vue.use(Tabs);
Vue.use(Icon);
Vue.use(Calendar);
Vue.use(Field);
Vue.use(Form);
Vue.use(Picker);
Vue.use(Progress);
Vue.use(CountDown);
Vue.use(Cell);
Vue.use(CellGroup);

Vue.config.productionTip = false

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
